import React from 'react'
import SubHeader from '../SubHeader'
import Header from '../Header'
import Footer from '../Properties/Footer'
import Page from './Page'
import Contact from './Contact'

function AllContact() {
  return (
    <div>
        <SubHeader/>
        <Header/>
        <Page/>
        <Contact/>
        <Footer/>
    </div>
  )
}

export default AllContact