import React, { Component } from 'react'
import { Col, Container, Row } from 'reactstrap'
import { CartProvider, useCart } from "react-use-cart";

import axios from 'axios'
import { NavLink } from 'react-router-dom'
class Product extends Component {
 state={
    products :[]
 }
 componentDidMount(){
    axios.get("https://dummyjson.com/products").then((element)=>{
         this.setState(
            {
                products : element.data.products
             }
         )
    })
 }
    render() {
    return (
        <div className="section properties">

<Container>
    <Row className="properties-box">
        {this.state.products.map((item)=>(
                           <div className="col-lg-4 col-md-6 align-self-center mb-30 properties-items col-md-6 adv">
                           <div className="item">
                               <NavLink to="/property"><img src={item.images[1]==null?item.images[0]:item.images[1]} alt="" /></NavLink>
                               <span className="category">{item.category}</span>
                               <h6>{item.price} $</h6>
                               <h4><NavLink to="/property">{item.title}</NavLink></h4>
                               <ul>
                                   <li>Rating: <span>{item.rating}</span></li>
                                   <li>Discount: <span>{item.discountPercentage}</span></li>
                                   <li>Stock: <span>{item.stock}</span></li>
                                   <li>Brand: <span>{item.brand}</span></li>
                                   <li>Sku: <span>{item.sku}</span></li>
                               </ul>
                               <div className="main-button">
                                   <NavLink to="/property">Schedule a visit</NavLink>
                               </div>
                           </div>
                       </div> 

        ))}
    </Row>
</Container>
      </div>
    )
  }
}
export default Product;
